"use cline"
import Link from 'next/link';
import { useState, useEffect } from 'react';
import { motion, AnimatePresence,easeInOut, easeOut  } from 'framer-motion';

// Couleurs inspirées de l'art africain
const colors = {
  primary: '#E9B826', // Or africain
  secondary: '#BB141A', // Rouge terre
  tertiary: '#2D5D2A', // Vert forêt
  dark: '#0A0A0A',   // Noir profond
  light: '#F5F5DC'   // Beige naturel
};


export default function Navigation() {
  const [isScrolled, setIsScrolled] = useState(false);
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);
  const [activeItem, setActiveItem] = useState('');

  // Détection du défilement pour changer le style de la nav
  useEffect(() => {
    const handleScroll = () => {
      setIsScrolled(window.scrollY > 50);
    };
    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  // Fermer le menu mobile lors du redimensionnement
  useEffect(() => {
    const handleResize = () => {
      if (window.innerWidth > 768) {
        setIsMobileMenuOpen(false);
      }
    };
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  const navItems = [
    { id: 'about', label: 'À PROPOS', path: '/about' },
    { id: 'projects', label: 'PROJETS', path: '/projects' },
    { id: 'culture', label: 'CULTURE', path: '/culture' },
    { id: 'contact', label: 'CONTACT', path: '/contact' },
  ];

 const menuVariants = {
  closed: {
    opacity: 0,
    y: -20,
    transition: {
      duration: 0.3,
      ease: easeInOut, // ✅ utiliser easing importé
    },
  },
  open: {
    opacity: 1,
    y: 0,
    transition: {
      duration: 0.4,
      ease: easeOut, // ✅ utiliser easing importé
      staggerChildren: 0.1,
      delayChildren: 0.2,
    },
  },
};

const itemVariants = {
  closed: { opacity: 0, x: -20 },
  open: { opacity: 1, x: 0 },
};

 

  return (
    <>
      <motion.header 
        className={`flex justify-between items-center p-4 fixed w-full z-50 transition-all duration-300 ${isScrolled ? 'py-3 backdrop-blur-md' : 'py-4'}`}
        initial={{ y: -100 }}
        animate={{ y: 0 }}
        transition={{ duration: 0.6, ease: "easeOut" }}
        style={{ 
          backgroundColor: isScrolled ? 'rgba(10, 10, 10, 0.95)' : 'rgba(10, 10, 10, 0.9)',
          borderBottom: isScrolled ? `1px solid ${colors.primary}20` : 'none'
        }}
      >
        {/* Logo */}
        <Link 
          href="/" 
          className="text-2xl font-bold flex items-center group"
          onMouseEnter={() => setActiveItem('home')}
          onMouseLeave={() => setActiveItem('')}
        >
          <motion.i 
            className="pi pi-crown mr-2" 
            style={{ color: colors.primary }}
            whileHover={{ rotate: 15, scale: 1.1 }}
            transition={{ type: "spring", stiffness: 400, damping: 10 }}
          ></motion.i> 
          <motion.span 
            style={{ color: colors.primary }}
            whileHover={{ color: colors.light }}
            transition={{ duration: 0.2 }}
          >
            KingTang
          </motion.span>
          {activeItem === 'home' && (
            <motion.span 
              className="absolute -bottom-1 left-0 w-full h-0.5"
              style={{ backgroundColor: colors.primary }}
              layoutId="navIndicator"
              transition={{ type: "spring", stiffness: 500, damping: 30 }}
            />
          )}
        </Link>

        {/* Navigation Desktop */}
        <nav className="hidden md:flex space-x-8 items-center">
          {navItems.map((item) => (
            <Link 
              key={item.id}
              href={item.path}
              className="relative group font-medium tracking-wider text-sm uppercase"
              onMouseEnter={() => setActiveItem(item.id)}
              onMouseLeave={() => setActiveItem('')}
              style={{ 
                color: activeItem === item.id ? colors.primary : colors.light,
                transition: 'color 0.3s ease'
              }}
            >
              {item.label}
              {(activeItem === item.id) && (
                <motion.span 
                  className="absolute -bottom-1 left-0 w-full h-0.5"
                  style={{ backgroundColor: colors.primary }}
                  layoutId="navIndicator"
                  transition={{ type: "spring", stiffness: 500, damping: 30 }}
                />
              )}
              {activeItem !== item.id && (
                <span 
                  className="absolute -bottom-1 left-0 w-0 h-0.5 transition-all duration-300 group-hover:w-full"
                  style={{ backgroundColor: colors.primary }}
                />
              )}
            </Link>
          ))}
          
          {/* Indicateur culturel */}
          <motion.div 
            className="flex items-center ml-4 px-3 py-1 rounded-full text-xs"
            style={{ backgroundColor: `${colors.primary}20`, color: colors.primary }}
            whileHover={{ scale: 1.05 }}
            transition={{ type: "spring", stiffness: 400, damping: 10 }}
          >
            <i className="pi pi-star mr-1"></i>
            <span>Culture Innovation</span>
          </motion.div>
        </nav>

        {/* Bouton Menu Mobile */}
        <motion.button 
          className="md:hidden p-2 rounded-lg"
          style={{ color: colors.light }}
          whileTap={{ scale: 0.9 }}
          onClick={() => setIsMobileMenuOpen(!isMobileMenuOpen)}
          aria-label="Menu mobile"
        >
          <AnimatePresence mode="wait">
            {isMobileMenuOpen ? (
              <motion.i 
                key="close"
                className="pi pi-times text-xl"
                initial={{ rotate: -90, opacity: 0 }}
                animate={{ rotate: 0, opacity: 1 }}
                exit={{ rotate: 90, opacity: 0 }}
                transition={{ duration: 0.2 }}
              />
            ) : (
              <motion.i 
                key="menu"
                className="pi pi-bars text-xl"
                initial={{ rotate: 90, opacity: 0 }}
                animate={{ rotate: 0, opacity: 1 }}
                exit={{ rotate: -90, opacity: 0 }}
                transition={{ duration: 0.2 }}
              />
            )}
          </AnimatePresence>
        </motion.button>
      </motion.header>

      {/* Menu Mobile */}
      <AnimatePresence>
        {isMobileMenuOpen && (
          <>
            <motion.div 
              className="fixed inset-0 bg-black bg-opacity-70 z-40 md:hidden"
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              exit={{ opacity: 0 }}
              onClick={() => setIsMobileMenuOpen(false)}
            />
            <motion.nav 
              className="fixed top-20 right-4 w-64 z-50 rounded-xl shadow-2xl md:hidden"
              style={{ backgroundColor: 'rgba(10, 10, 10, 0.95)', backdropFilter: 'blur(10px)' }}
              variants={menuVariants}
              initial="closed"
              animate="open"
              exit="closed"
            >
              <div className="p-4 border-b" style={{ borderColor: `${colors.primary}30` }}>
                <div className="flex items-center px-3 py-2 rounded-lg" style={{ backgroundColor: `${colors.primary}10` }}>
                  <i className="pi pi-user mr-3" style={{ color: colors.primary }}></i>
                  <span style={{ color: colors.primary }}>Navigation</span>
                </div>
              </div>
              <div className="p-4">
                {navItems.map((item) => (
                  <motion.div 
                    key={item.id}
                    variants={itemVariants}
                    className="mb-2 last:mb-0"
                  >
                    <Link 
                      href={item.path}
                      className="flex items-center p-3 rounded-lg transition-all"
                      style={{ 
                        backgroundColor: activeItem === item.id ? `${colors.primary}20` : 'transparent',
                        color: activeItem === item.id ? colors.primary : colors.light
                      }}
                      onClick={() => {
                        setIsMobileMenuOpen(false);
                        setActiveItem(item.id);
                      }}
                      onMouseEnter={() => setActiveItem(item.id)}
                      onMouseLeave={() => setActiveItem('')}
                    >
                      <i className={`pi pi-${item.id === 'projects' ? 'briefcase' : item.id === 'culture' ? 'palette' : item.id === 'contact' ? 'envelope' : 'user'} mr-3`}></i>
                      {item.label}
                    </Link>
                  </motion.div>
                ))}
                
                {/* Indicateur culturel mobile */}
                <motion.div 
                  variants={itemVariants}
                  className="flex items-center justify-center mt-6 p-3 rounded-lg text-xs"
                  style={{ backgroundColor: `${colors.primary}20`, color: colors.primary }}
                >
                  <i className="pi pi-star mr-2"></i>
                  <span>Innovation Culturelle</span>
                </motion.div>
              </div>
            </motion.nav>
          </>
        )}
      </AnimatePresence>
    </>
  );
}